<template>
  <div style="position: absolute;width: 100%;height: 100%">
    <transition name="plus-icon">
      <div v-if="!taskReady" class="xyjuzhong" style="position:fixed;width: 100%;height: 100%;z-index: 9999;background-color: rgba(0,0,0,.2)">
        <div style="width: 60%;border-radius: 10px;background-color: #fff;font-size: 17px">
          <div style="padding: 10px;text-align: left"></div>
          <div class="xyjuzhong" style="justify-content: flex-end;padding: 10px">
            <div @click="get_task" class="bt_material"></div>
          </div>
        </div>

      </div>
    </transition>
    <transition name="plus-icon">
      <div v-if="showtaskChange" class="xyjuzhong" style="flex-direction: column;position:fixed;width: 100%;height: 100%;z-index: 9999;background-color: rgba(0,0,0,.2)">
        <div style="width: 60%;border-radius: 10px;background-color: #fff;">
          <div @click="selectTask(select_task_id)" class="setpopitem name-border">
            <div :style="{'color':search_task_id!=='0'?'rgba(24,103,192,1)':'#000000'}" style="width: 100%">{{select_task_name}}</div>
          </div>
          <div @click="selectTask('0')" class="setpopitem">
            <div :style="{'color':search_task_id==='0'?'rgba(24,103,192,1)':'#000000'}" style="width: 100%"></div>
          </div>
        </div>
        <div style="width: 80%;padding-top: 40px;font-size: 15px;color: #ffffff">    </div>
      </div>
    </transition>
<!--    <div class="xyjuzhong" style="position:absolute;bottom: 0;width: 100%;z-index: 4" :style="{height:(innerHeight*0.05+60)+'px'}">-->
<!--      <div @click="testspeed" class="xyjuzhong ceshi_bt" :style="{width:(innerHeight*0.05+50)+'px',height:(innerHeight*0.05+50)+'px'}"></div>-->
<!--    </div>-->
<!--    <div v-if="taskReady" class="dingshiBox" :style="{top:(StatusbarHeight+5)+'px'}">-->
<!--      <van-switch @change="ds_switchChange" v-model="open_map_ds_sx" size="24px"/>-->
<!--    </div>-->
    <div v-if="taskReady&&dingshiqiReady" class="shizhongBox" :style="{top:(StatusbarHeight+5)+'px'}">
      <img v-if="!timing_feresh_init" src="../assets/imgs/time.png" style="width: 26px;height: 26px">
      <img v-else src="../assets/imgs/timeS.png" style="width: 26px;height: 26px">
    </div>
    <div v-if="taskReady" class="renwuBox" :style="{top:(StatusbarHeight+5)+'px'}">{{search_task_id==='0'?'':select_task_name}}</div>
    <img @click="tomylocal" :style="{bottom:(innerHeight*0.05+60)+'px'}" style="position: absolute;right: 10px;z-index: 5;width: 30px;height: 30px;padding: 10px;" src="../assets/imgs/mydw.png" />
    <img @click="refresh" :style="{bottom:innerHeight*0.05+'px'}" style="position: absolute;right: 10px;z-index: 5;width: 30px;height: 30px;padding: 10px;" src="../assets/refresh.png" />
    <div id="stopD" @click="goDetail" :style="{'z-index':showdetail?5:0,'padding-top':StatusbarHeight+'px'}" style="display: flex;align-items: center;position: absolute;width: 100%;background-color: rgba(255,255,255,.9);">
      <div :style="{height:innerHeight*0.1+'px'}" style="width: 100%;border-bottom-style:solid;border-bottom-width: 1px;border-bottom-color: rgba(0,0,0,.1);display: flex;justify-content: space-between;">
        <div style="width: 20%;display: flex;align-items: center;justify-content: center">
          <div v-if="stop.hasOwnProperty('isrec')" style="font-size: 20px;font-weight: 400;" :style="{color:stop.isrec?'#00CD00':'#FF0000'}">{{stop.isrec?'':''}}</div>
          <div v-else style="font-size: 15px;font-weight: 400;color: #8F8F94;">N/A</div>
        </div>
        <div style="flex-direction: column;display: flex;width: 60%;justify-content: space-around;">
          <div style="font-size: 20px;font-weight: 700;">{{stop.name||'N/A'}}</div>
          <div style="font-size: 15px;font-weight: 700;color: #8F8F94;">{{stop.mname||'N/A'}}</div>
        </div>
        <div @click="goMap" style="width: 20%;display: flex;align-items: center;height: 100%;position: relative">
<!--          <a :href="mapUrl" style="width: 100%;height: 100%;position: absolute;z-index: 6"></a>-->
          <img style="width: 90%;" src="../assets/imgs/daohang.png" />
        </div>

      </div>
    </div>
    <div style="position: absolute;z-index: 1;width: 100%;height:100%">
      <l-map
        @click="cleanStops"
        @update:center="updateCenter"
        style="width: 100%;height:100%"
        :center="center"
        :zoom="14"
        ref="map">
        <l-tile-layer :url="providerOptions.url" :options="providerOptions.options"></l-tile-layer>
        <l-marker :lat-lng="dingwei">
          <l-icon :icon-anchor="staticAnchor">
            <img src="../assets/imgs/dw.png" style="width:30px;height:30px;" >
          </l-icon>
        </l-marker>
        <l-circle
        :lat-lng="circle.center"
        :radius="circle.radius"
        :weight="circle.weight"
      ></l-circle>
        <div v-for="(item,index) in stopDatas">
          <l-marker :lat-lng="item.center" @click="showStop(item,index)">
            <l-icon :icon-anchor="staticAnchor">
              <div style="width: 30px;height: 30px;border-radius: 100%" :style="{'box-shadow':item.isshow?'rgb(136, 136, 136) 0px 5px 5px':'0px 0px 0px #888888'}">
                <img v-if="item.isrec" src="../assets/imgs/stopB.png" :style="{width:item.isshow?'30px':'26px',height:item.isshow?'30px':'26px'}" >
                <img v-else src="../assets/imgs/stopC.png" :style="{width:item.isshow?'30px':'26px',height:item.isshow?'30px':'26px'}" >
              </div>
            </l-icon>
          </l-marker>
        </div>
      </l-map>
    </div>

  </div>
</template>

<script>
  import L from 'leaflet'
  import {} from  'leaflet.chinatmsproviders'
  import {
      LMap,
      LTileLayer,LMarker,
      LPopup,LIcon,LCircle
  } from 'vue2-leaflet'
  import { mapGetters,mapState } from 'vuex'
    export default {
      name: "googleMap",
      data(){
          return {
            taskReady:true,
            dingshiqiReady:false,
            showtaskChange:false,
            select_task_name:'',
				    select_task_id:'',
            search_task_id:'',
            mapUrl:'',
            showdetail:false,
            staticAnchor: [16, 16],
            currentVehicle:{route_info:{}},
            innerHeight:100,
            StatusbarHeight:24,
            stopDatas:[],
            stop:{},
            setIntervalStop:null,
            jwt:'',
            lon:120.686186,
            lat:24.146341,
            my_lat:24.146341,
            my_lon:120.686186,
            range:'1500',
            center:L.latLng(24.146341,120.686186),
            circle:{
              center: L.latLng(24.146341,120.686186),
              radius: 1500,
              weight:1
            },
            dingwei:L.latLng(24.146341,120.686186),
            timing_feresh_init:null,
            is_ds_fresh:false,
            pageShow:true,
            open_map_ds_sx:false
          }
      },
      components:{LMap,
            LTileLayer, LMarker,
            LPopup,LIcon,LCircle
        },
      computed:{
        ...mapState(['providerOptions'])
      },
      beforeDestroy() {
        this.pageShow = false
        this.dingshishuaxin()
      },
      created(){
        window['referesh_stops'] = (data) => {
          this.referesh_stops(data)
        }
        window['referesh_task'] = () => {
          this.refresh(false)
        }
        window['timing_feresh'] = (status) => {
          this.pageShow = status
          // ds_switchChangeswich
          this.open_timing_feresh(status)
        }
        this.range=this.$route.query.range||'1500';
        const mapss = this.$route.query.map;
        this.lat=this.$route.query.lat||24.146341;
        this.lon=this.$route.query.lon||120.686186;
        this.my_lat=this.$route.query.lat||24.146341;
        this.my_lon=this.$route.query.lon||120.686186;
        this.center=L.latLng(this.lat,this.lon)
        this.circle={
          center: L.latLng(this.lat,this.lon),
          radius: Number(this.range),
          weight:1
        }
        this.dingwei=L.latLng(this.lat,this.lon)
        this.$store.commit('setProvider',mapss)
        this.jwt=this.$route.query.jwt
      },
      async mounted(){
        window.testJavaJS = this.testJavaJS
          let me = this
          delete L.Icon.Default.prototype._getIconUrl;
          L.Icon.Default.mergeOptions({
              iconRetinaUrl: require('leaflet/dist/images/marker-icon-2x.png'),
              iconUrl: require('leaflet/dist/images/marker-icon.png'),
              shadowUrl: require('leaflet/dist/images/marker-shadow.png'),
          });
          this.innerHeight = window.innerHeight
        var sd=document.getElementsByClassName("leaflet-top");
        sd[0].style.top=(window.innerHeight-100)+'px';
        let temp_timeing = false
        me.$nextTick(()=>{
          if (window.plus){
            temp_timeing = plus.storage.getItem('open_map_ds_sx') || false
            me.select_task_name=plus.storage.getItem('select_task_name')
            me.select_task_id=plus.storage.getItem('select_task_id')
            me.search_task_id=me.select_task_id
          }else{
            temp_timeing = window.localStorage.getItem('open_map_ds_sx') || false
            me.select_task_name=window.localStorage.getItem('select_task_name')
            me.select_task_id=window.localStorage.getItem('select_task_id')
            me.search_task_id=me.select_task_id
          }
          if(me.select_task_id){
            me.taskReady=true
            me.getStops(me.range)
            if(temp_timeing){
              me.pageShow = temp_timeing
              me.open_timing_feresh(temp_timeing)
            }
          }else{
            if(window.plus){
              plus.nativeUI.closeWaiting()
            }
            me.taskReady=false
          }
        })
        setTimeout(()=>{
          me.setStatusbarHeight(3)
        },1000)
        setTimeout(()=>{
          me.dingshiqiReady = true
        },3000)
      },
      methods:{
        testspeed(){
          if (window.plus){
            plus.nativeUI.showWaiting('...')
          }
          let nowTM = new Date().getTime()
          let me=this;
          let testList = []
          for(let i=0;i<10;i++){
            let testpp = new Promise((resolve)=>{
              me.$apis.getStops({task_id:1,range:15000,lon:121.44287109375001,lat:24.82537857544687,jwt:this.jwt}).then((resp)=>{
                resolve(true)
              }).catch((err)=>{

              })
            })
            testList.push(testpp)
          }
          Promise.all(testList).then((resP)=>{
            if (window.plus){
              plus.nativeUI.closeWaiting()
            }

            alert('' + (new Date().getTime() - nowTM) + '');
          }).catch((errP)=>{
            if (window.plus){
              plus.nativeUI.closeWaiting()
            }
          })

        },
        referesh_stops(data){
          let new_data = JSON.parse(data)
          if(this.showdetail){
            this.stop.lastinspecttime = new_data.record_time
            this.stop.isrec = new_data.isrec
          }
          for(let i=0;i<this.stopDatas.length;i++){
						if(new_data.e_stopid===this.stopDatas[i].estop_id){
							this.stopDatas[i].lastinspecttime=new_data.record_time
							this.stopDatas[i].isrec=new_data.isrec
						}
					}
        },
        goMap(){
          window.location.href=this.mapUrl
        },
        tomylocal(){
          let me=this
          if(window.plus){
            plus.nativeUI.showWaiting('')
          }
          plus.geolocation.getCurrentPosition(function(p){
            me.my_lat = p.coords.latitude
            me.my_lon = p.coords.longitude
            me.changemyLocal()
          }, function(e){
            alert(': ' + e.message);
            me.changemyLocal()
          }, {enableHighAccuracy:true} );
        },
        changemyLocal(){
          this.lat=this.my_lat;
          this.lon=this.my_lon
          this.center=L.latLng(this.my_lat,this.my_lon)
          this.circle={
            center: L.latLng(this.my_lat,this.my_lon),
            radius: Number(this.range),
            weight:1
          }
          this.dingwei=L.latLng(this.my_lat,this.my_lon)
          this.refresh(false)
        },
        selectTask(id){
          this.search_task_id=id
          this.showtaskChange=false
          this.refresh()
        },
        testJavaJS(){
          TestFirstApp.showToash('Toast');
        },
        get_task(){
          TestFirstApp.showToash('Toast');
          if (window.plus){
            this.select_task_name=plus.storage.getItem('select_task_name')
            this.select_task_id=plus.storage.getItem('select_task_id')
            this.search_task_id=this.select_task_id
          }else{
            this.select_task_name=window.localStorage.getItem('select_task_name')
            this.select_task_id=window.localStorage.getItem('select_task_id')
            this.search_task_id=this.select_task_id
          }
          if(!this.select_task_id){
            this.taskReady=false
            uni.switchTab({
              url: '/pages/taskPage/taskPage'

            });
          }else{
            if(window.plus){
              plus.nativeUI.showWaiting('')
            }
            this.taskReady=true
            this.getStops(this.range)
          }
        },
        // // 
        // ds_switchChange(e){
        //   plus.storage.setItem('open_map_ds_sx', e);
        //   this.open_timing_feresh(e)
        // },
        // 
        open_timing_feresh(status){
          if(status){
            if(this.timing_feresh_init){
              clearInterval(this.timing_feresh_init)
              this.timing_feresh_init = null
            }
            this.$nextTick(()=>{
              if(this.select_task_id){
                this.dingshishuaxin()
                this.timing_feresh_init= setInterval(()=>{
                  this.dingshishuaxin()
                },60000)
              }
            })
          }else{
            if(this.timing_feresh_init){
              clearInterval(this.timing_feresh_init)
              this.timing_feresh_init = null
            }
          }
        },
        dingshishuaxin(){
          if(this.pageShow){
            if(window.plus){
              if(!this.is_ds_fresh){
                // this.changemyLocal()
                plus.geolocation.getCurrentPosition((p)=>{
                  this.my_lat = p.coords.latitude
                  this.my_lon = p.coords.longitude
                  this.is_ds_fresh = false
                  this.changemyLocal()
                }, (e)=>{
                  this.is_ds_fresh = false
                  this.changemyLocal()
                }, {enableHighAccuracy:true} );
              }
            }
          }else{
            if(this.timing_feresh_init){
              clearInterval(this.timing_feresh_init)
              this.timing_feresh_init = null
            }
          }


        },
        refresh(showloading=true){
          if (window.plus){
            this.range =plus.storage.getItem('range')
            this.jwt=plus.storage.getItem('jwt')
            this.select_task_name=plus.storage.getItem('select_task_name')
            this.select_task_id=plus.storage.getItem('select_task_id')
          }else{
            this.range = window.localStorage.getItem('range')
            this.jwt=window.localStorage.getItem('jwt')
            this.select_task_name=window.localStorage.getItem('select_task_name')
            this.select_task_id=window.localStorage.getItem('select_task_id')
          }
          if(this.search_task_id!=='0'){
            this.search_task_id=this.select_task_id
          }
          this.circle={
            center: L.latLng(this.lat,this.lon),
            radius: Number(this.range),
            weight:1
          }
          if(showloading&&window.plus){
            plus.nativeUI.showWaiting('')
          }
          this.getStops(this.range)
        },
        getStops(ran){
          // let nowTM = new Date().getTime()
          let me=this;
          this.$apis.getStops({task_id:parseInt(this.search_task_id),range:ran,lon:this.lon,lat:this.lat,jwt:this.jwt}).then((resp)=>{
            if(window.plus){
              plus.nativeUI.closeWaiting()
            }
            // alert(': ' + resp.data.length + '\n' + (new Date().getTime() - nowTM) + '');
            me.stopDatas = resp.data.map((item)=>{
              item.center=L.latLng(item.lat, item.lon);
              return item
            })
          }).catch((err)=>{
            if(window.plus){
              plus.nativeUI.closeWaiting()
            }
          })
        },
        setStatusbarHeight(n){
          if(window.plus){
            this.StatusbarHeight=plus.os.name==='iOS'?0:plus.navigator.getStatusbarHeight()
          }
        },
        goDetail(){
          // if(window.UniAppJSBridge){
            delete this.stop.center
            let data=JSON.stringify(this.stop)

            uni.navigateTo({
              url: '/pages/stopdetail/stopdetail?page_name=task&stop='+data

            });
          // }
        },
        showStop(data,index){
          this.stop=data;
          if(window.plus){
            if(plus.os.name==='iOS'){
              this.mapUrl=`comgooglemaps://?daddr=${this.stop.lat},${this.stop.lon}&directionsmode=driving`
            }else{
              this.mapUrl = `google.navigation:q=${this.stop.lat},${this.stop.lon}`;
            }
          }
          // this.center=data.center;
          this.stopDatas = this.stopDatas.map((item,indx)=>{
            if(index===indx){
              item.isshow=true
            }else {
              item.isshow=false
            }
              return item
            });
          this.showdetail=true
        },
        updateCenter(e){
          this.center=L.latLng(e.lat,e.lng)
        },
        cleanStops(event){
          let e=event.latlng
          this.lat=e.lat
          this.lon=e.lng
          this.center=L.latLng(e.lat,e.lng)
          this.circle={
            center: L.latLng(e.lat,e.lng),
            radius: Number(this.range),
            weight:1
          }
          this.dingwei=L.latLng(e.lat,e.lng)
          this.showdetail=false
          this.stopDatas = this.stopDatas.map((item)=>{
              item.isshow=false
              return item
            })
        },
      }
    }
</script>

<style>
  html, body { height: 100%; }
  .st-container{
        font-size: small;

        background-color: aliceblue;
        width: 100%;
        padding: 2px 10px;

    }
  .leaflet-bottom{
    z-index: -1;
  }
  .renwuBox{
    color: rgba(0,0,0,.6);
    position: absolute;
    left: 10px;
    z-index: 3;
    line-height: 20px;
    font-size: 20px;
    font-weight: 700;
    /*border-bottom: 2px solid rgba(24,103,192,1);*/
  }
  .shizhongBox{
    position: absolute;
    right: 10px;
    z-index: 3;
    opacity: 0.6;
  }
  .dingshiBox{
    color: rgba(0,0,0,.6);
    position: absolute;
    right: 10px;
    z-index: 3;
    line-height: 20px;
    font-size: 20px;
    font-weight: 700;
    /*border-bottom: 2px solid rgba(24,103,192,1);*/
  }
  /*.renwuBox:active{*/
  /*  color:rgba(24,103,192,1)*/
  /*}*/
  .xyjuzhong{
    display: flex;
    justify-content: center;
    align-items: center;
  }
  .bt_material{
		padding: 5px 8px;
		border-radius: 5px;
		background-color: rgba(24,103,192,0);
		transition:background-color 0.1s;
		letter-spacing: 2px;
    color: rgba(24,103,192,1);
	}
	.bt_material:active{
		background-color: rgba(24,103,192,.3);
	}
  .setpopitem{
		padding: 5px 10px;
		height: 50px;
		font-size: 20px;
		position: relative;
		display: flex;
		align-items: center;
		transition:background-color 0.1s;
		border-radius: 8px;
	}
	.setpopitem:active{
		background-color: #EEEEEE;
	}
  .name-border {
	  position: relative;
	}
	.name-border:after {
	    content: " ";
	    width: 200%;
	    height: 200%;
	    position: absolute;
	    top: 0;
	    left: 0;
	    border-bottom: 0.5px solid rgba(0, 0, 0, .5);
	    -webkit-transform: scale(0.5);
	    transform: scale(0.5);
	    -webkit-transform-origin: 0 0;
	    transform-origin: 0 0;
	    box-sizing: border-box;
		pointer-events:none;
	}
  .plus-icon-enter-active{
    transition: opacity .5s;
  }
  .plus-icon-enter{
     opacity: 0;
  }
  .plus-icon-leave-active{
    transition: opacity .5s;
  }
  .plus-icon-leave-to{
    opacity: 0;
  }
  .ceshi_bt{
    border-radius: 100%;background-color: #fff;box-shadow: 0 0 15px rgba(0,0,0,.2);
  }
  .ceshi_bt:active{
    box-shadow:none;
  }
</style>



